<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="学生数据">
      <!-- 标题及操作区 -->
      <div slot="extra">

      </div>

      <!-- 主要内容 -->
      <div>
        <el-row :gutter="20">
          <!-- 左侧 -->
          <el-col :span="7">
            <div class="overview">
              <el-card>
                <div class="detail">
                  <p class="detail-text">平均就业薪资</p>
                  <p class="detail-num">{{ overview.salary }}</p>
                </div>
                <i class="el-icon-s-finance icon"></i>
              </el-card>
              <el-card>
                <div class="detail">
                  <p class="detail-text">班级人数</p>
                  <p class="detail-num">{{ overview.student_count }}</p>
                </div>
                <i class="el-icon-user icon"></i>
              </el-card>
              <el-card>
                <div class="detail">
                  <p class="detail-text">平均年龄</p>
                  <p class="detail-num">{{ overview.age }}</p>
                </div>
                <i class="el-icon-date icon"></i>
              </el-card>
              <el-card>
                <div class="detail">
                  <p class="detail-text">小组个数</p>
                  <p class="detail-num">{{ overview.group_count }}</p>
                </div>
                <i class="el-icon-files icon"></i>
              </el-card>
            </div>
            <div class="salaryData1">
              <el-card>
                <div ref="salaryMan" style="height: 280px;"></div>
                <div ref="salaryMiss" style="height: 280px;"></div>
              </el-card>
            </div>
          </el-col>
          <!-- 右侧 -->
          <el-col :span="17">
            <!-- 薪资走势折线图 -->
            <el-card class="el-card-mothe">
              <div ref="salaryline" style="height: 300px;"></div>
            </el-card>

            <!-- 班级薪资分布 -->
            <el-card>
              <div ref="classnum" style="height: 300px;"></div>
            </el-card>

            <!-- 每组薪资 -->
            <el-card class="classgroup">
              <div class="header-group">
                <p>每组薪资</p>
                <div>
                  <p>第{{group}}组</p>
                  <button @click="changeGroup(i)" v-for="(i, index) in 8"
                :key="index">{{ i }}</button>
                </div>
              </div>
              <div ref="classgroup" style="height: 300px;"></div>
            </el-card>

            <!-- 籍贯分布 -->
            <el-card>
              <div ref="nativeplace" style="height: 550px;"></div>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </Headercomponer>
  </div>
</template>

<script>
import * as echarts from 'echarts'
import 'echarts/extension/bmap/bmap'
import { showStudentData } from '@/api/student'
import Headercomponer from '@/components/headercomponer.vue'
import axios from 'axios'
export default {
  data () {
    return {
      overview: {},
      year: [],
      salaryData: [],
      groupData: {},
      classGroup: [],
      provinceData: [],
      group: 1

    }
  },
  async created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '学生数据'
    })

    // 获取学生数据
    const { data: { data } } = await showStudentData()
    // console.log(data)
    this.overview = data.overview
    this.year = data.year
    this.salaryData = data.salaryData
    this.groupData = data.groupData
    this.classGroup = data.groupData['1']
    this.provinceData = data.provinceData

    // 男薪资发布扇形图
    const arrMan = []
    const arrMiss = []
    this.salaryData.forEach(i => {
      arrMan.push({
        name: i.label,
        value: i.g_count
      })
      arrMiss.push({
        name: i.label,
        value: i.b_count
      })
    })
    const myChart = echarts.init(this.$refs.salaryMan)
    // 男
    myChart.setOption({
      title: {
        text: '男女薪资分布'
      },
      legend: {
        top: '5%',
        left: 'center'
      },
      tooltip: {
        trigger: 'item'
      },
      series: [
        {
          name: '男',
          type: 'pie',
          radius: ['40%', '70%'],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          data: arrMan
        }
      ]
    })
    // 女
    const myChartMiss = echarts.init(this.$refs.salaryMiss)
    myChartMiss.setOption({
      legend: {
        top: '0%',
        left: 'center'
      },
      tooltip: {
        trigger: 'item'
      },
      series: [
        {
          emphasis: {
            label: {
              show: true,
              fontSize: 40,
              fontWeight: 'bold'
            }
          },
          label: {
            show: false,
            position: 'center'
          },
          name: '女',
          type: 'pie',
          radius: ['40%', '70%'],
          avoidLabelOverlap: false,
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          data: arrMiss
        }
      ]
    })

    // 薪资走势
    const arrMonth = []
    const arrSalary = []
    this.year.forEach(i => {
      arrMonth.push(i.month)
      arrSalary.push(i.salary)
    })
    // console.log(arrSalary)
    const mySalaryLine = echarts.init(this.$refs.salaryline)
    mySalaryLine.setOption({
      title: {
        text: '薪资走势'
      },
      xAxis: {
        type: 'category',
        data: arrMonth
      },
      // yAxis: {
      //   type: 'value'
      // },
      yAxis: {},
      series: [
        {
          data: arrSalary,
          type: 'line',
          smooth: true
        }
      ]
    })

    // 薪资分布
    const arrClass = []
    this.salaryData.forEach(i => {
      arrClass.push({
        name: i.label,
        value: i.g_count + i.b_count
      })
    })
    const myClassNum = echarts.init(this.$refs.classnum)
    myClassNum.setOption({
      title: {
        text: '班级薪资分布'
      },
      // legend: {
      //   top: '5%',
      //   left: 'center'
      // },
      tooltip: {
        trigger: 'item'
      },
      series: [
        {
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          name: '薪资范围------人数',
          type: 'pie',
          data: arrClass
        }
      ]
    })

    // 每组薪资
    this.changeGroup()

    // 籍贯分布
    const myNativePlace = echarts.init(this.$refs.nativeplace)
    myNativePlace.showLoading()
    const ROOT_PATH = 'https://echarts.apache.org/examples'
    axios.get(ROOT_PATH + '/data/asset/geo/CHN.json')
      .then((response) => {
        myNativePlace.hideLoading()
        echarts.registerMap('CHN', response.data, {
          // 可选的地理区域配置
          Alaska: {
            left: -131,
            top: 25,
            width: 15
          },
          Hawaii: {
            left: -110,
            top: 28,
            width: 5
          },
          'Puerto Rico': {
            left: -76,
            top: 26,
            width: 2
          }
        })
        myNativePlace.setOption({
          title: {
            text: '中国人口估计（2012年）',
            subtext: '来自 www.census.gov',
            sublink: 'http://www.census.gov/popest/data/datasets.html',
            left: 'right'
          },
          tooltip: {
            trigger: 'item',
            showDelay: 0,
            transitionDuration: 0.2
          }
        })
      })
      .catch(() => {})
  },
  methods: {
    changeGroup (i) {
      if (i) {
        this.group = i
        this.classGroup = this.groupData[`${i}`]
      }

      // 每组薪资
      const myClassGroup = echarts.init(this.$refs.classgroup)
      myClassGroup.setOption({
        tooltip: {},
        // 注明
        legend: {
          data: ['期望薪资', '就业薪资']
        },
        // x轴
        xAxis: {
          data: this.classGroup.map(i => i.name),
          type: 'category'
        },
        // y轴
        yAxis: [
          {
            type: 'value'
          }
        ],
        // 数据
        series: [
          {
            name: '期望薪资',
            data: this.classGroup.map(i => i.hope_salary),
            type: 'bar'
          },
          {
            name: '就业薪资',
            data: this.classGroup.map(i => i.salary),
            type: 'bar'
          }
        ]
      })
    }
  },
  components: {
    Headercomponer
  }
}
</script>

<style lang="less" scoped>
.overview{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
  .icon{
    width: 80px;
    height: 80px;
    font-size: 40px;
    text-align: center;
    line-height: 80px;
    background-color: #9999;
    border-radius: 50%;
    color: white;
    float: right;
  }
  .detail{
    display: flex;
    flex-direction: column; //上下
    justify-content: center;
    float: left;
  }
  .detail-text{
    font-size: 20px;
    margin-bottom: 10px;
      color: #9999;
  }
  .detail-num{
      font-size: 30px;
      margin-bottom: 20px;
      line-height: 30px;
      height: 30px;
  }
  .el-card{
    width: 49%;
    margin-bottom: 20px;
  }
}

.el-card-mothe{
  width: 60%;
  float: left;
  margin-right: 10px;

}

.classgroup{
  margin-top: 20px;
  margin-bottom: 15px;
}
.header-group{
  width: 100%;
  display: flex;
  p{
    flex: 0.9;
    font-size: 19px;
    font-weight: bold;
    color: rgb(70, 70, 70);
  }
  div{
    display: flex;
    p{
      flex: 1;
      margin-right: 5px;
    }
  }
  button{
    width: 30px;
    height: 30px;
    background: white;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    font-size: 19px;
    color: #82828299;
    font-weight: bold;
    transition: all .3s;
    float: right;
  }
  button:active{
    background-color: rgb(0, 178, 0);
    color: white;
  }
}
</style>
